<template>
  <UserHeader :uid=this.id></UserHeader>
  <br><br>
</template>

<script>
import UserHeader from "@/components/user/UserHeader.vue";
import {showArticles} from "@/api";

export default {
  name: "UserArticle",
  components: {UserHeader},
  data() {
    return{
      id: this.$route.query.id,
      size: "",
      current: (this.$route.query.page===undefined)?1:this.$route.query.page,
      total: "",
      articles: []
    }
  },
  mounted() {
    showArticles(this.id).then(res=>{
      this.size=res.data.object.length
      this.total=Math.ceil(this.size/10)
      if(res.data.code===200){
        for(let i=0; i<this.size; i++){
          let article=res.data.object[i]
          this.articles.push({
            articleId: article.articleId,
            articleUserId: article.articleUserId,
            articleTypeId: article.articleTypeId,
            articleTitle: article.articleTitle,
            articleTime: article.articleTime,
            articleView: article.articleView,
            articleLike: article.articleLike,
            articleAudit: article.articleAudit
          })
        }
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  methods: {
    nextPage: function (){
      this.$router.push({
        name: 'UserArticle',
        query: {
          id: this.id,
          page: this.current+1
        }
      })
    },
    prePage: function (){
      this.$router.push({
        name: 'UserArticle',
        query: {
          id: this.id,
          page: this.current-1
        }
      })
    }
  }
}
</script>

