<template>
  <h1>输入用户名</h1>
  <br><br>
  <input id="userName" type="text" v-model="userName" placeholder="请输入用户名" />
  <br><br>
  <button @click="send()">下一步</button>
</template>

<script>
import {getUserByName, sendEmailCode} from "@/api";

export default {
  name: "PwdForget",
  data(){
    return{
      userName: "",
      userEmail: "",
      isSend: ""
    }
  },
  methods:{
    send: function (){
      getUserByName(this.userName).then(res=>{
        if(res.data.code===200){
          this.userEmail=res.data.object.userEmail
        }
        else{
          alert(res.data.msg)
        }
      }).catch(e=>{
        console.log(e)
      })
      sendEmailCode(this.userEmail).then(res=>{
        if(res.data.code===200){
          this.isSend='yes'
        }else{
          this.isSend='no'
        }
        this.$router.push({
          name: 'EmailCode',
          query:{
            email: this.userEmail,
            message: res.data.msg,
            isSend: this.isSend,
            use: 'pwdChange'
          }
        })
      }).catch(e=>{
        console.log(e)
      })
    }
  }
}
</script>

