export const commentAdd=(comment)=>{
    let fd=new FormData();
    fd.append("commentId","0");
    fd.append("commentUserId",comment.userId);
    fd.append("commentContext",comment.context);
    fd.append("commentLike","0");
    fd.append("commentTopId",comment.topId);
    fd.append("commentForId",comment.forId);
    fd.append("commentType",comment.type);
    if(comment.type==="article"){
        fd.append("commentArticleId",comment.id);
    }
    else if(comment.type==="forward"){
        fd.append("commentForwardId",comment.id);
    }
    return axios.post('/comment/add',fd)
}

