export const articleAdd=(article)=>{
    let fd=new FormData();
    fd.append("articleId","0");
    fd.append("articleUserId",article.userId);
    fd.append("articleTypeId",article.typeId);
    fd.append("articleTitle",article.title);
    fd.append("articleContext",article.context);
    fd.append("articleView","0");
    fd.append("articleComment","0");
    fd.append("articleLike","0");
    fd.append("articleCollect","0");
    fd.append("articleForward","0");
    fd.append("articleAudit","0");
    return axios.post('/article/add',fd)
}

