<template>

</template>

<script>
import {
  collectAdd, collectDelete,
  collectIf, commentAdd,
  concernByUser,
  concernDelete,
  concernIf,
  getArticle,
  getArticleType,
  getUser, likeAdd, likeDelete,
  likeIf, showComments
} from "@/api";

export default {
  name: "ArticleDetail",
  data() {
    return {
      article: {
        articleId: "",
        articleUserId: "",
        articleTypeId: "",
        articleTitle: "",
        articleContext: "",
        articleTime: "",
        articleView: "",
        articleComment: "",
        articleLike: "",
        articleCollect: "",
        articleForward: "",
      },
      user: {
        userId: "",
        userName: "",
        userImg: ""
      },
      articleType: {
        articletypeId: "",
        articletypeTag: ""
      },
      my: this.user.userId===this.$store.getters['user'].userId,
      comment: {
        userId: "",
        context: "",
        topId: "",
        forId: "",
        type: ""
      },
      comments: []
    }
  },
  mounted() {
    getArticle(this.$route.query.aid).then(res=>{
      if(res.data.code===200){
        this.article.articleId=res.data.object.articleId
        this.article.articleUserId=res.data.object.articleUserId
        this.article.articleTypeId=res.data.object.articleTypeId
        this.article.articleTitle=res.data.object.articleTitle
        this.article.articleContext=res.data.object.articleContext
        this.article.articleTime=res.data.object.articleTime
        this.article.articleView=res.data.object.articleView
        this.article.articleComment=res.data.object.articleComment
        this.article.articleLike=res.data.object.articleLike
        this.article.articleCollect=res.data.object.articleCollect
        this.article.articleForward=res.data.object.articleForward
      }
    }).catch(e=>{
      console.log(e)
    })

    getUser(this.article.articleUserId).then(res=>{
      if(res.data.code===200){
        this.user.userId=res.data.object.userId
        this.user.userName=res.data.object.userName
        this.user.userImg=res.data.object.userImg
      }
    }).catch(e=>{
      console.log(e)
    })

    getArticleType(this.article.articleTypeId).then(res=>{
      if(res.data.code===200){
        this.articleType.articletypeId=res.data.object.articletypeId
        this.articleType.articletypeTag=res.data.object.articletypeTag
      }
    }).catch(e=>{
      console.log(e)
    })

    showComments(this.$route.query.aid, "article").then(res=>{
      if(res.data.code===200){
        let size=res.data.object.length
        for(let i=0; i<size; i++){
          let comm=res.data.object[i];
          this.comments.push({
            commentId: comm.commentId
          })
        }
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  computed: {
    concernIs() {
      concernIf(this.user.userId, this.$store.getters['user'].userId).then(res=>{
        return res.data.object
      })
    },
    likeArticleIs() {
      likeIf(this.article.articleId, this.$store.getters['user'].userId, "article").then(res=>{
        return res.data.object
      })
    },
    collectIs() {
      collectIf(this.$store.getters['user'].userId, this.article.articleId).then(res=>{
        return res.data.object
      })
    }
  },
  methods: {
    concernAdd: function (){
      concernByUser(this.user.userId, this.$store.getters['user'].userId)
    },
    concernDel: function (){
      concernDelete(this.user.userId, this.$store.getters['user'].userId)
    },
    collect: function (){
      collectAdd(this.$store.getters['user'].userId, this.article.articleId)
    },
    collectDel: function (){
      collectDelete(this.$store.getters['user'].userId, this.article.articleId)
    },
    likeArticle: function (){
      likeAdd(this.article.articleId, this.$store.getters['user'].userId, "article")
    },
    likeArticleDel: function (){
      likeDelete(this.article.articleId, this.$store.getters['user'].userId, "article")
    },
    goArticleType: function (){
      this.$router.push({
        name: 'ArticleTypeDetail',
        query: {
          atid: this.articleType.articletypeId
        }
      })
    },
    commentForArticle: function (){
      this.comment.userId=this.$store.getters['user'].userId
      this.comment.topId=null
      this.comment.forId=null
      this.comment.type="article"
      commentAdd(this.comment)
    }
  }

}
</script>

