<template>
  <h1>{{user.userName}}}</h1>
  <br><br>
  <button @click="goHome()">主页</button>
  <br><br>
  <button @click="goArticle()">投稿</button>
  <br><br>
  <button @click="goCollect()">收藏</button>
  <br><br>
  <button @click="goLike()">点赞</button>
  <br><br>
  <button @click="search()">搜索</button>
  <br><br>
  <button @click="goConcern()">关注数：{{user.userConcern}}</button>
  <br><br>
  <button @click="goFan()">粉丝数：{{user.userFans}}</button>
  <br><br>
  获赞数：{{user.userLike}}
  <br><br>
  阅读数：{{user.userView}}}
</template>

<script>
import {concernByUser, concernDelete, concernIf, getUser} from "@/api";

export default {
  name: "UserHome",
  props: ['uid'],
  data() {
    return{
      id: this.uid,
      my: this.id===this.$store.getters['user'].userId,
      str: "",
      user: {
        userId: "",
        userName: "",
        userSex: "",
        userStatus: "",
        userImg: "",
        userFans: "",
        userConcern: "",
        userLike: "",
        userView: ""
      }
    }
  },
  mounted() {
    getUser(this.id).then(res=>{
      if(res.data.code===200){
        this.user.userId=res.data.object.userId
        this.user.userName=res.data.object.userName
        this.user.userSex=res.data.object.userSex
        this.user.userStatus=res.data.object.userStatus
        this.user.userImg=res.data.object.userImg
        this.user.userFans=res.data.object.userFans
        this.user.userConcern=res.data.object.userConcern
        this.user.userLike=res.data.object.userLike
        this.user.userView=res.data.object.userView
        this.$store.dispatch('changeUser',{
          user: res.data.object
        })
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  computed: {
    concernIs() {
      concernIf(this.id, this.$store.getters['user'].userId).then(res=>{
        return res.data.object
      })
    }
  },
  methods: {
    goHome: function (){
      this.$router.push({
        name: 'UserHome',
        query: {
          id: this.id
        }
      })
    },
    goArticle: function (){
      this.$router.push({
        name: 'UserArticle',
        query: {
          id: this.id
        }
      })
    },
    goCollect: function (){
      this.$router.push({
        name: 'UserCollect',
        query: {
          id: this.id
        }
      })
    },
    goLike: function (){
      this.$router.push({
        name: 'UserLike',
        query: {
          id: this.id
        }
      })
    },
    search: function (){
      this.$router.push({
        name: 'UserSearch',
        query: {
          id: this.id,
          str: this.str
        }
      })
    },
    goConcern: function (){
      this.$router.push({
        name: 'UserConcern',
        query: {
          id: this.id
        }
      })
    },
    goFan: function (){
      this.$router.push({
        name: 'UserFan',
        query: {
          id: this.id
        }
      })
    },
    concernAdd: function (){
      concernByUser(this.id, this.$store.getters['user'].userId)
    },
    concernDel: function (){
      concernDelete(this.id, this.$store.getters['user'].userId)
    },
    img: function (){
      if(this.my){
        /**修改图像**/
      }
      else{
        /**查看图象**/
      }
    }
  }
}
</script>

