<template>
  <div class="container" style="height: 100%; padding-bottom: 800px;">
    <el-container>
      <div class="blue">
      <el-header></el-header>
      </div>
      <el-main>
        <el-row>
          <el-col :span="16">
            <div>
              <el-row>
                <el-col :span="22">
                  <el-input v-model="str" placeholder="搜索..."></el-input>
                </el-col>
                <el-col :span="2">
                  <el-button slot="append" icon="el-icon-search" @click="search">搜索</el-button>
                </el-col>
              </el-row>
            </div>
            <div>
              <el-row>
                <el-col :span="2">首字母:</el-col>
                <el-col :span="22">
                  <el-radio-group v-model="text" @change="initial">
                    <el-radio-button label="#">#</el-radio-button>
                    <el-radio-button label="a">A</el-radio-button>
                    <el-radio-button label="b">B</el-radio-button>
                    <el-radio-button label="c">C</el-radio-button>
                    <el-radio-button label="d">D</el-radio-button>
                    <el-radio-button label="e">E</el-radio-button>
                    <el-radio-button label="f">F</el-radio-button>
                    <el-radio-button label="g">G</el-radio-button>
                    <el-radio-button label="h">H</el-radio-button>
                    <el-radio-button label="i">I</el-radio-button>
                    <el-radio-button label="g">J</el-radio-button>
                    <el-radio-button label="k">K</el-radio-button>
                    <el-radio-button label="l">L</el-radio-button>
                    <el-radio-button label="m">M</el-radio-button>
                    <el-radio-button label="n">N</el-radio-button>
                    <el-radio-button label="o">O</el-radio-button>
                    <el-radio-button label="p">P</el-radio-button>
                    <el-radio-button label="q">Q</el-radio-button>
                    <el-radio-button label="r">R</el-radio-button>
                    <el-radio-button label="s">S</el-radio-button>
                    <el-radio-button label="t">T</el-radio-button>
                    <el-radio-button label="u">U</el-radio-button>
                    <el-radio-button label="v">V</el-radio-button>
                    <el-radio-button label="w">W</el-radio-button>
                    <el-radio-button label="x">X</el-radio-button>
                    <el-radio-button label="y">Y</el-radio-button>
                    <el-radio-button label="z">Z</el-radio-button>
                  </el-radio-group>
                </el-col>
              </el-row>
            </div>
            <div>
              <el-row :gutter="10">
                <el-col
                    v-for="tcm in tcms"
                    :span="5"
                    :offset="1"
                >
                  <TcmItem :tcm="tcm"></TcmItem>
                  <br><br>
                </el-col>
              </el-row>
            </div>
            <div>
              <el-pagination
                  @current-change="changePage"
                  v-model:current-page="this.page"
                  :default-current-page="parseInt(this.page)"
                  hide-on-single-page
                  :page-size="20"
                  background
                  layout="prev, pager, next, jumper"
                  :total="parseInt(this.total)"
              ></el-pagination>
            </div>
          </el-col>
          <el-col :span="6" :offset="2">
            <div>
              <el-row>按归经分类:</el-row>
              <el-row>
                <el-radio-group v-model="text" @change="inductive">
                  <el-radio-button label="#">全部</el-radio-button>
                  <el-radio-button label="胃">胃经</el-radio-button>
                  <el-radio-button label="脾">脾经</el-radio-button>
                  <el-radio-button label="肾">肾经</el-radio-button>
                  <el-radio-button label="大肠">大肠经</el-radio-button>
                </el-radio-group>
              </el-row>
              <el-row>
                <el-radio-group v-model="text" @change="inductive">
                  <el-radio-button label="肺">肺经</el-radio-button>
                  <el-radio-button label="心">心经</el-radio-button>
                  <el-radio-button label="肝">肝经</el-radio-button>
                  <el-radio-button label="胆">胆经</el-radio-button>
                  <el-radio-button label="膀胱">膀胱经</el-radio-button>
                </el-radio-group>
              </el-row>
              <el-row>
                <el-radio-group v-model="text" @change="inductive">
                  <el-radio-button label="三焦">三焦经</el-radio-button>
                  <el-radio-button label="小肠">小肠经</el-radio-button>
                  <el-radio-button label="心包">心包经</el-radio-button>
                </el-radio-group>
              </el-row>
            </div>
            <div>
              <el-row>按四性分类:</el-row>
              <el-row>
                <el-radio-group v-model="text" @change="property">
                  <el-radio-button label="#">全部</el-radio-button>
                  <el-radio-button label="寒">寒</el-radio-button>
                  <el-radio-button label="凉">凉</el-radio-button>
                  <el-radio-button label="温">温</el-radio-button>
                  <el-radio-button label="热">热</el-radio-button>
                </el-radio-group>
              </el-row>
            </div>
            <div>
              <el-row>按五味分类:</el-row>
              <el-row>
                <el-radio-group v-model="text" @change="taste">
                  <el-radio-button label="#">全部</el-radio-button>
                  <el-radio-button label="酸">酸</el-radio-button>
                  <el-radio-button label="苦">苦</el-radio-button>
                  <el-radio-button label="甘">甘</el-radio-button>
                  <el-radio-button label="辛">辛</el-radio-button>
                  <el-radio-button label="咸">咸</el-radio-button>
                </el-radio-group>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-main>
      <el-footer class="red"></el-footer>
    </el-container>
  </div>
</template>

<script>
import {selectTcm, showAllTcm, showByInductive, showByInitial, showByProperty, showByTaste} from "@/api";
import TcmItem from "@/components/Item/TcmItem.vue";

export default {
  name: "TcmHome",
  components: {TcmItem},
  data() {
    return{
      str: "",
      text: "",
      mode: "original",
      page: 1,
      total: "",
      tcms:[]
    }
  },
  mounted() {
    showAllTcm(this.page).then(res=>{
      this.total=res.data.object.total
      if(res.data.code===200){
        for(let i=0; i<res.data.object.list.length; i++){
          let tcm=res.data.object.list[i]
          this.tcms.push({
            tcmId: tcm.tcmId,
            tcmNameZh: tcm.tcmNameZh
          })
        }
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  methods: {
    search: function (){
      this.mode="search"
      this.tcms=[]
      this.page=1
      selectTcm(this.str, this.page).then(res=>{
        this.total=res.data.object.total
        console.log(res.data.object.pageSize)
        if(res.data.code===200){
          for(let i=0; i<res.data.object.list.length; i++){
            let tcm=res.data.object.list[i]
            this.tcms.push({
              tcmId: tcm.tcmId,
              tcmNameZh: tcm.tcmNameZh
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    initial: function (){
      this.mode="initial"
      this.tcms=[]
      this.page=1
      showByInitial(this.text, this.page).then(res=>{
        this.total=res.data.object.total
        if(res.data.code===200){
          for(let i=0; i<res.data.object.list.length; i++){
            let tcm=res.data.object.list[i]
            this.tcms.push({
              tcmId: tcm.tcmId,
              tcmNameZh: tcm.tcmNameZh
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    inductive: function (){
      this.mode="inductive"
      this.tcms=[]
      this.page=1
      showByInductive(this.text, this.page).then(res=>{
        this.total=res.data.object.total
        if(res.data.code===200){
          for(let i=0; i<res.data.object.list.length; i++){
            let tcm=res.data.object.list[i]
            this.tcms.push({
              tcmId: tcm.tcmId,
              tcmNameZh: tcm.tcmNameZh
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    taste: function (){
      this.mode="taste"
      this.tcms=[]
      this.page=1
      showByTaste(this.text, this.page).then(res=>{
        this.total=res.data.object.total
        if(res.data.code===200){
          for(let i=0; i<res.data.object.list.length; i++){
            let tcm=res.data.object.list[i]
            this.tcms.push({
              tcmId: tcm.tcmId,
              tcmNameZh: tcm.tcmNameZh
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    property: function (){
      this.mode="property"
      this.tcms=[]
      this.page=1
      showByProperty(this.text, this.page).then(res=>{
        this.total=res.data.object.total
        if(res.data.code===200){
          for(let i=0; i<res.data.object.list.length; i++){
            let tcm=res.data.object.list[i]
            this.tcms.push({
              tcmId: tcm.tcmId,
              tcmNameZh: tcm.tcmNameZh
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    changePage: function () {
      this.tcms=[]
      if(this.mode==="original"){
        showAllTcm(this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
      else if(this.mode==="initial"){
        showByInitial(this.text, this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
      else if(this.mode==="inductive"){
        showByInductive(this.text, this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
      else if(this.mode==="taste"){
        showByTaste(this.text, this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
      else if(this.mode==="property"){
        showByProperty(this.text, this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
      else if(this.mode==="search"){
        selectTcm(this.str, this.page).then(res=>{
          this.total=res.data.object.total
          if(res.data.code===200){
            for(let i=0; i<res.data.object.list.length; i++){
              let tcm=res.data.object.list[i]
              this.tcms.push({
                tcmId: tcm.tcmId,
                tcmNameZh: tcm.tcmNameZh
              })
            }
          }
        }).catch(e=>{
          console.log(e)
        })
      }
    }
  }
}
</script>

<style scoped>
.bg-purple { background: #d3dce6 !important; }
.bg-purple-light { background: #e5e9f2; }
.red {background: crimson;}
.blue {background: blue;}
