<template>

</template>

<script>
import {changeUserEmail} from "@/api";

export default {
  name: "EmailChange",
  data() {
    return {
      id: this.$route.query.id,
      email: ""
    }
  },
  methods: {
    change: function (){
      changeUserEmail(this.id,this.email).then(res=>{
        alert(res.data.msg)
        if(res.data.code===200){
          this.$store.dispatch('changeUser',{
            user: res.data.object
          })
          this.$router.push({
            name: 'ChangeInfo'
          })
        }
      }).catch(e=>{
        console.log(e)
      })
    }
  }
}
</script>

