<template>
  <UserHeader :uid=this.id></UserHeader>
  <br><br>
</template>

<script>
import UserHeader from "@/components/user/UserHeader.vue";
import {showFans} from "@/api";

export default {
  name: "UserFans",
  components: {UserHeader},
  data() {
    return{
      id: this.$route.query.id,
      size: "",
      current: (this.$route.query.page===undefined)?1:this.$route.query.page,
      total: "",
      users:[]
    }
  },
  mounted() {
    showFans(this.id).then(res=>{
      this.size=res.data.object.length
      this.total=Math.ceil(this.size/10)
      if(res.data.code===200){
        for(let i=0; i<this.size; i++){
          let user=res.data.object[i]
          this.users.push({
            userId: user.userId,
            userName: user.userName,
            userSex: user.userSex,
            userImg: user.userImg,
            userFans: user.userFans,
            userConcern: user.userConcern
          })
        }
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  methods: {
    nextPage: function (){
      this.$router.push({
        name: 'UserFan',
        query: {
          id: this.id,
          page: this.current+1
        }
      })
    },
    prePage: function (){
      this.$router.push({
        name: 'UserFan',
        query: {
          id: this.id,
          page: this.current-1
        }
      })
    }
  }
}
</script>

