<template>
  确认旧密码: <input id="oldPwd" type="password" v-model="oldPwd" placeholder="请输入旧密码"/>
  <br><br>
  <button @click="confirmPwd()">确定</button>
</template>

<script>
import {confirmOldPwd} from "@/api";

export default {
  name: "PwdConfirm",
  data() {
    return {
      id: this.$route.query.id,
      oldPwd: ""
    }
  },
  methods: {
    confirmPwd: function (){
      confirmOldPwd(this.id, this.oldPwd).then(res=>{
        if(res.data.code===200){
          this.$router.push({
            name: 'PwdChange',
            query: {
              id: this.id
            }
          })
        }
        else{
          alert(res.data.msg)
        }
      }).catch(e=>{
        console.log(e)
      })
    }
  }
}
</script>

