<template>
  <el-card :body-style="{ padding: '0px' }">
    <img
        src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png"
        class="image"
    />
    <div style="padding: 10px">
      <el-link :underline="false" @click="showDetail">{{tcm.tcmNameZh}}</el-link>
    </div>
  </el-card>
</template>

<script>
export default {
  name: "TcmItem",
  props: ['tcm'],
  data() {
    return {
      tcmItem: {
        tcmId: this.tcm.tcmId,
        tcmNameZh: this.tcm.tcmNameZh
      }
    }
  },
  methods: {
    showDetail: function (){
      this.$router.push({
        name: 'TcmDetail',
        query: {
          tid: this.tcmItem.tcmId
        }
      })
    }
  }
}
</script>

