<template>

</template>

<script>
import {changeUserInfo, getUser, sendEmailCode} from "@/api";

export default {
  name: "ChangeInfo",
  data() {
    return {
      isSend: "",
      user:{
        userId: "",
        userName: "",
        userSex: "",
        userEmail: "",
        userPhone: "",
        userStatus: "",
        registerTime: "",
        userBirthday: "",
      }
    }
  },
  mounted() {
    getUser(this.$store.getters['user'].userId).then(res=>{
      if(res.data.code===200){
        this.user.userId=res.data.object.userId
        this.user.userName=res.data.object.userName
        this.user.userSex=res.data.object.userSex
        this.user.userEmail=res.data.object.userEmail
        this.user.userPhone=res.data.object.userPhone
        this.user.userStatus=res.data.object.userStatus
        this.user.registerTime=res.data.object.registerTime
        this.user.userBirthday=res.data.object.userBirthday
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  methods: {
    pwdChange: function (){
      this.$router.push({
        name: 'PwdConfirm',
        query: {
          id: this.user.userId
        }
      })
    },
    emailChange: function (){
      sendEmailCode(this.user.userEmail).then(res=>{
        if (res.data.code === 200) {
          this.isSend='1'
        } else {
          this.isSend='0'
        }
        this.$router.push({
          name: 'EmailCode',
          query: {
            email: this.user.userEmail,
            message: res.data.msg,
            isSend: this.isSend,
            use: 'emailChange'
          }
        })
      }).catch(e=>{
        console.log(e)
      })
    },
    phoneChange: function (){
      this.$router.push({
        name: 'PhoneChange',
        query: {
          id: this.user.userId
        }
      })
    },
    change: function (){
      changeUserInfo(this.user).then(res=>{
        alert(res.data.msg)
        if(res.data.code===200){
          this.$store.dispatch('changeUser',{
            user: res.data.object
          })
          this.$router.push({
            name: 'ChangeInfo'
          })
        }
      }).catch(e=>{
        console.log(e)
      })
    }
  }
}
</script>

