<template>
  <div>
    <br><br>
    新密码: <input id="password1" type="password" v-model="password1" placeholder="请输入新密码"/>
    <br><br>
    确认新密码: <input id="password2" type="password" v-model="password2" placeholder="请再次输入新密码"/>
    <br><br>
    <button @click="ChangePwd()">确定修改</button>
  </div>
</template>

<script>
import {changePwd} from "@/api";

export default {
  name: 'PwdChange',
  data() {
    return {
      password1: "",
      password2: ""
    }
  },
  methods: {
    ChangePwd: function () {
      if (this.password1 === this.password2) {
        changePwd(this.$route.query.id, this.password1).then(res=>{
          alert(res.data.msg)
          if (res.data.code === 200) {
            if(this.$store.getter['isLogin']===true){
              this.$router.push({
                name: 'ChangeInfo'
              })
            }
            else{
              this.$router.push({
                name: 'UserLogin'
              })
            }
          }
        }).catch(e => {
          console.log(e);
        })
      } else {
        alert("两次输入的密码不同")
      }
    }
  }
}
</script>


