<template>
  <UserHeader :uid=this.id></UserHeader>
  <br><br>
  个人资料
  <br><br>
  UID：{{user.userId}}
  生日：{{user.userBirthday}}
  性别：{{user.userSex}}
  状态：{{user.userStatus}}
  <br><br>
  <button v-if=myIf @click="changeInfo()">修改资料</button>
  <br><br>
  <button @click="goArticle()">查看全部</button>
  <br><br>
  <button @click="goCollect()">查看全部</button>
  <br><br>
  <button @click="goLike()">查看全部</button>
</template>

<script>
import UserHeader from "@/components/user/UserHeader.vue";
import {getUser, showArticles, showCollects, showLikes, userDelete} from "@/api";
export default {
  name: "UserHome",
  components: {UserHeader},
  data() {
    return{
      id: this.$route.query.id,
      myIf: this.id===this.$store.getters['user'].userId,
      user: {
        userId: "",
        userName: "",
        userSex: "",
        userStatus: "",
        userImg: "",
        userFans: "",
        userConcern: "",
        userLike: "",
        userView: ""
      },
      my: "",
      myArticles: [],
      collect: "",
      collectArticles: [],
      like: "",
      likeArticles: [],
    }
  },
  mounted() {
    getUser(this.id).then(res=>{
      if(res.data.code===200){
        this.user.userId=res.data.object.userId
        this.user.userName=res.data.object.userName
        this.user.userSex=res.data.object.userSex
        this.user.userStatus=res.data.object.userStatus
        this.user.userImg=res.data.object.userImg
        this.user.userFans=res.data.object.userFans
        this.user.userConcern=res.data.object.userConcern
        this.user.userLike=res.data.object.userLike
        this.user.userView=res.data.object.userView
        this.$store.dispatch('changeUser',{
          user: res.data.object
        })
      }
    }).catch(e=>{
      console.log(e)
    })

    showLikes(this.id).then(res=>{
      this.like=res.data.object.length
      if(res.data.code===200){
        if(this.like>5){
          for(let i=0; i<5; i++){
            let article=res.data.object[i]
            this.likeArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
        else{
          for(let i=0; i<this.like; i++){
            let article=res.data.object[i]
            this.likeArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
      }
    }).catch(e=>{
      console.log(e)
    })

    showArticles(this.id).then(res=>{
      this.my=res.data.object.length
      if(res.data.code===200){
        if(this.my>5){
          for(let i=0; i<5; i++){
            let article=res.data.object[i]
            this.myArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
        else{
          for(let i=0; i<this.my; i++){
            let article=res.data.object[i]
            this.myArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
      }
    }).catch(e=>{
      console.log(e)
    })

    showCollects(this.id).then(res=>{
      this.collect=res.data.object.length
      if(res.data.code===200){
        if(this.collect>5){
          for(let i=0; i<5; i++){
            let article=res.data.object[i]
            this.collectArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
        else{
          for(let i=0; i<this.collect; i++){
            let article=res.data.object[i]
            this.collectArticles.push({
              articleId: article.articleId,
              articleUserId: article.articleUserId,
              articleTypeId: article.articleTypeId,
              articleTitle: article.articleTitle,
              articleTime: article.articleTime,
              articleView: article.articleView,
              articleLike: article.articleLike,
              articleAudit: article.articleAudit
            })
          }
        }
      }
    }).catch(e=>{
      console.log(e)
    })
  },
  methods: {
    changeInfo: function (){
      this.$router.push({
        name: 'ChangeInfo'
      })
    },
    delete: function (){
      userDelete(this.id).then(res=>{
        if(res.data.code===200){
          this.$store.dispatch('logout')
          this.$router.push({
            name: 'UserLogin'
          })
        }
      })
    },
    goArticle: function (){
      this.$router.push({
        name: 'UserArticle',
        query: {
          id: this.id
        }
      })
    },
    goCollect: function (){
      this.$router.push({
        name: 'UserCollect',
        query: {
          id: this.id
        }
      })
    },
    goLike: function (){
      this.$router.push({
        name: 'UserLike',
        query: {
          id: this.id
        }
      })
    }
  }
}
</script>

