<template>
  <div>
    用户名或邮箱:<input type="text" v-model="text" placeholder="请输入用户名或邮箱" />
    <br><br>
    密码:<input type="password" v-model="password" placeholder="请输入密码" />
    <br><br>
    <button @click="login()">登录</button>
    <br><br>

    <router-link to='/Register'>
      <span style="text-align:center;font-size: 14px;">账号注册</span>
    </router-link>

    <router-link to='/PwdForget'>
      <span style="text-align:center;font-size: 14px; color:red;" @click="forget()">忘记密码</span>
    </router-link>

  </div>
</template>

<script>
import {sendEmailCode, userLogin} from "@/api";

export default {
  name: 'UserLogin',
  data() {
    return {
      text: "",
      password: "",
      isSend: ""
    }
  },
  methods: {
    login: function() {
      userLogin(this.text, this.password).then(res => {
        alert(res.data.msg)
        if (res.data.code === 200) {
          this.$store.dispatch("login",{
            user: res.data.object,
            success: () => {
              console.log(res.data.msg)
            }
          })
          this.$router.push({
            name: 'Success'
          })
        }
        else if(res.data.code===400){
          sendEmailCode(res.data.object).then(re=>{
            if(re.data.code===200){
              this.isSend='yes'
            } else{
              this.isSend='no'
            }
            this.$router.push({
              name: 'EmailCode',
              query: {
                email: res.data.object,
                message: re.data.msg,
                isSend: this.isSend,
                use: 'activate'
              }
            })
          }).catch(e=>{
            console.log(e)
          })
        }
      }).catch(e => {
        console.log(e)
      })
    }
  }
}
</script>
