<template>
  <h1>{{message}}</h1>
  <br><br>
  <input id="code" type="text" v-model="code" v-if="this.isSend==='yes'" placeholder="请输入验证码" />
  <br><br>
  <button v-if="this.isSend==='yes'" @click="confirm()">确认</button>
  <button @click="resend()">重新发送</button>
  <button v-if="this.isSend==='no'" @click="back()">返回登陆页面</button>
</template>

<script>
import {confirmEmailCode, sendEmailCode} from "@/api";

export default {
  name: "EmailCode",
  data() {
    return{
      code: "",
      message: this.$route.query.message,
      isSend: this.$route.query.isSend,
      isSend1: ""
    }
  },
  methods:{
    confirm: function (){
      confirmEmailCode(this.$route.query.email, this.code).then(res=>{
        alert(res.data.msg)
        if(res.data.code===200){
          if(this.$route.query.use==='activate'){
            this.$store.dispatch('login',{
              user: res.data.object,
              success: () => {
                console.log(res.data.msg)
              }
            })
            this.$router.push({
              name: 'Success'
            })
          }
          else if(this.$route.query.use==='pwdChange'){
           this.$router.push({
             name: 'PwdChange',
             query: {
               id: res.data.object.userId
             }
           })
          }
          else if(this.$route.query.user==='emailChange'){
            this.$router.push({
              name: 'EmailChange',
              query: {
                id: res.data.object.userId
              }
            })
          }
        }
      }).catch(e=>{
        console.log(e)
      })
    },
    resend: function (){
      sendEmailCode(this.$route.query.email).then(res=> {
        if (res.data.code === 200) {
          this.isSend1='1'
        } else {
          this.isSend1='0'
        }
        this.$router.push({
          name: 'EmailCode',
          query: {
            email: this.$route.query.email,
            message: res.data.msg,
            isSend: this.isSend1,
            use: this.$route.query.use
          }
        })
      }).catch(e=>{
        console.log(e)
      })
    },
    back: function (){
      this.$router.push({
        name: 'Login'
      })
    }
  }
}
</script>

