import {createRouter, createWebHistory} from "vue-router";
import UserLogin from '@/components/user/UserLogin.vue'
import UserRegister from "@/components/user/UserRegister.vue";
import Success from "@/components/Success.vue";
import Home from "@/components/Home.vue";
import PwdForget from "@/components/user/PwdForget.vue";
import EmailCode from "@/components/user/EmailCode.vue";
import PwdChange from "@/components/user/PwdChange.vue";
import TcmHome from "@/components/tcm/TcmHome.vue";
import UserHome from "@/components/user/UserHome.vue";
import UserArticle from "@/components/user/UserArticle.vue";
import UserCollect from "@/components/user/UserCollect.vue";
import UserLike from "@/components/user/UserLike.vue";
import UserSearch from "@/components/user/UserSearch.vue";
import UserConcern from "@/components/user/UserConcern.vue";
import UserFan from "@/components/user/UserFan.vue";
import ChangeInfo from "@/components/user/ChangeInfo.vue";
import PwdConfirm from "@/components/user/PwdConfirm.vue";
import EmailChange from "@/components/user/EmailChange.vue";
import PhoneChange from "@/components/user/PhoneChange.vue";
import TcmDetail from "@/components/tcm/TcmDetail.vue";
import ArticleDetail from "@/components/article/ArticleDetail.vue";
import ArticleTypeDetail from "@/components/article/ArticleTypeDetail.vue";

const routes =[
    {
        path: '/h',
        name: 'Home',
        component: Home
    },
    {
        path: '/UserLogin',
        name: 'UserLogin',
        component: UserLogin
    },
    {
        path: '/UserRegister',
        name: 'UserRegister',
        component: UserRegister
    },
    {
        path: '/EmailCode',
        name: 'EmailCode',
        component: EmailCode
    },
    {
        path: '/PwdForget',
        name: 'PwdForget',
        component: PwdForget
    },
    {
        path: '/PwdChange',
        name: 'PwdChange',
        component: PwdChange
    },
    {
        path: '/UserHome',
        name: 'UserHome',
        component: UserHome
    },
    {
        path: '/UserArticle',
        name: 'UserArticle',
        component: UserArticle
    },
    {
        path: '/UserCollect',
        name: 'UserCollect',
        component: UserCollect
    },
    {
        path: '/UserLike',
        name: 'UserLike',
        component: UserLike
    },
    {
        path: '/UserSearch',
        name: 'UserSearch',
        component: UserSearch
    },
    {
        path: '/UserConcern',
        name: 'UserConcern',
        component: UserConcern
    },
    {
        path: '/UserFan',
        name: 'UserFan',
        component: UserFan
    },
    {
        path: '/ChangeInfo',
        name: 'ChangeInfo',
        component: ChangeInfo
    },
    {
        path: '/PwdConfirm',
        name: 'PwdConfirm',
        component: PwdConfirm
    },
    {
        path: '/EmailChange',
        name: 'EmailChange',
        component: EmailChange
    },
    {
        path: '/PhoneChange',
        name: 'PhoneChange',
        component: PhoneChange
    },
    {
        path: '/',
        name: 'TcmHome',
        component: TcmHome
    },
    {
        path: '/TcmDetail',
        name: 'TcmDetail',
        component: TcmDetail
    },
    {
        path: '/ArticleDetail',
        name: 'ArticleDetail',
        component: ArticleDetail
    },
    {
        path: '/ArticleTypeDetail',
        name: 'ArticleTypeDetail',
        component: ArticleTypeDetail
    },
    {
        path: '/Success',
        name: 'Success',
        component: Success
    }
]

const router=createRouter({
    history: createWebHistory(),
    routes,
})

export default router

