<template>
  <div>
    用户名: <input id="name" type="text" v-model="user.name" placeholder="请输入用户名" />
    <br><br>
    性别: <input type="radio" id="male" value="男" v-model="user.sex" />
    <label for="male">男</label>
    <input type="radio" id="female" value="女" v-model="user.sex" />
    <label for="female">女</label>
    <br><br>
    邮箱: <input id="email" type="text" v-model="user.email" placeholder="请输入邮箱" />
    <br><br>
    手机号码: <input id="phone" type="text" v-model="user.phone" placeholder="请输入手机号码" />
    <br><br>
    密码: <input id="password1" type="password" v-model="user.password1" placeholder="请输入密码" />
    确认密码: <input id="password2" type="password" v-model="user.password2" placeholder="请再次输入密码" />
    <br><br>
    <button @click="register()">下一步</button>
    <br><br>

    <router-link to='/'>
      <span style="text-align:center;font-size: 14px; color:red;">现在登录</span>
    </router-link>
  </div>
</template>

<script>
import {sendEmailCode, userRegister} from "@/api";

export default {
  name: 'UserRegister',
  data() {
    return {
      user: {
        name: "",
        password1: "",
        password2: "",
        sex: "",
        email: "",
        phone: "",
        birthday: "2001-06-05"
      },
      isSend: ""
    }
  },
  methods: {
    register: function() {
      if (this.password1 === this.password2) {
        userRegister(this.user).then(res => {
          alert(res.data.msg)
          if (res.data.code === 200) {
            sendEmailCode(this.user.email).then(re=>{
              if(re.data.code===200){
                this.isSend='yes'
              } else{
                this.isSend='no'
              }
              this.$router.push({
                name: 'EmailCode',
                query: {
                  email: this.user.email,
                  message: re.data.msg,
                  isSend: this.isSend,
                  use: 'activate'
                }
              })
            }).catch(e=>{
              console.log(e)
            })
          }
        }).catch(e => {
          console.log(e)
        })
      } else {
        alert("两次输入的密码不同")
      }
    }
  }
}
</script>

<!-- 添加“scoped”属性以将CSS仅限于此组件 -->
